import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Modal, Button } from 'react-bootstrap';
import emailjs from 'emailjs-com';

function InquiryForm() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [inquiry, setInquiry] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [submitText, setSubmitText] = useState('Submit');

  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitText('Sending...');
    
    const templateParams = {
      name,
      email,
      inquiry
    };

    emailjs.send('service_0lui7bd', 'template_kare7m4', templateParams, 'zt8xzi8Fs0VsyttHE')
      .then((response) => {
        console.log('Success:', response);
        setSubmitText('Submit');
        setShowModal(true);
        // Clear form fields after successful submission
        setName('');
        setEmail('');
        setInquiry('');
      }, (error) => {
        console.error('Error:', error);
        setSubmitText('Submit');
        alert('There was an error sending your message. Please try again later.');
      });
  };

  return (
    <div className="card inquiry-form">
      <div className="card-body">
        <h5 className="card-title text-center">Contact Us</h5>
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="name">Name</label>
            <input
              type="text"
              className="form-control"
              id="name"
              placeholder="Enter your name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="email">Email address</label>
            <input
              type="email"
              className="form-control"
              id="email"
              placeholder="Enter your email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="inquiry">Inquiry</label>
            <textarea
              className="form-control"
              id="inquiry"
              rows="4"
              placeholder="Enter your inquiry"
              value={inquiry}
              onChange={(e) => setInquiry(e.target.value)}
              required
            ></textarea>
          </div>
          <button type="submit" className="btn btn-primary">{submitText}</button>
        </form>
      </div>

      {/* Confirmation Modal */}
      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Inquiry Sent</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="text-center">
            <i className="fas fa-check-circle fa-3x text-success"></i>
            <p>Your inquiry has been sent successfully!</p>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={() => setShowModal(false)}>
            Okay
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default InquiryForm;
